import {
		mapState,
    } from 'vuex';
import md5 from '../../kayida/common/md5.js'

export default {
	computed: {
	            ...mapState(['userInfo'])  
	        },
	data() {
		return {
			banner:'',
			video:''
		}
	},
	methods: {
		
	},
	onLoad() {
		this.request.httpRequest('api/course/center','GET').then(res =>{
			this.banner = res.data.retval.banner
			this.video = res.data.retval.list_data[0].video
		})
	}
}
